import React from 'react';
import { Route, Switch } from 'react-router-dom';

import LeftMenu from './leftmenu';
import PostList from './content-postlist';
import PostDetail from './content-postdetail';
import CategoryList from './content-category';
import About from './about';
import Contact from './contact';

import axios from 'axios';
import * as api from '../../api';

class Index extends React.Component {
    state = {
        loaded: false,
        posts: [],
        cats: [],
    }

    componentDidMount() {
        this.loadposts();
    }

    loadposts() {
        const post_list = api.api_post_list + "&maxResults=500";
        axios.get(post_list)
        .then(resp => {
            resp.data.items.map((value) => {
                const content = value.content;
                const parser = new DOMParser();
                const html = parser.parseFromString(content, 'text/html');
                const img = html.querySelector('img');
                const url = img ? img.src : '';
                var post = {
                    title: value.title,
                    date: value.published.slice(0,10),
                    image: url,
                    id: value.id,
                    avatar: value.author.image.url,
                    content: value.content,
                    labels: value.labels
                }
                this.setState(prevState => ({
                    posts: [...prevState.posts, post]
                }))
            });
            var mycats = [];
            var datas = this.state.posts;
            for (var i = 0; i < datas.length; i++)
            {
                var xxxs = datas[i].labels;
                for (var e = 0; e < xxxs.length; e++)
                {
                    if(!mycats.includes(xxxs[e]))
                    {
                        mycats.push(xxxs[e]);
                    }
                }
            }
            console.log(mycats);
            this.setState({
                loaded: true,
                cats: mycats,
            });
        })
    }

    render() {
        return (
            <div id="container" className="active">
                <LeftMenu cats={this.state.cats} />
                <div id="content">
                    <div class="inner-content">
                        <Switch>
                            <Route exact path="/" component={() => <PostList posts={this.state.posts} />} />
                            <Route exact path="/about" component={() => <About />} />
                            <Route exact path="/contact" component={() => <Contact />} />
                            <Route exact path="/post/:postid" component={(props) => <PostDetail {...props} />} />
                            <Route exact path="/category/:catid" component={(props) => <CategoryList {...props} />} />
                        </Switch>
                    </div>
                </div>
            </div>
        )
    }
}

export default Index;