import React from 'react';
import { Link } from 'react-router-dom';


export default function StandardPost(props) {
    return (
        <div class="blog-post image-post">
            <div class="inner-post">
                <img alt="" src={props.img} />
                <div class="post-content">
                    <h2><Link to={props.to}>{props.title}</Link></h2>
                    <ul class="post-tags">
                        <li><a href="#"><i class="fa fa-heart"></i>99 likes</a></li>
                        <li><a href="#"><i class="fa fa-comment"></i>9 comments</a></li>
                        <li><a href="#"><i class="fa fa-calendar"></i>{props.date}</a></li>
                    </ul>
                </div>
            </div>
        </div>
    )
}