import React from 'react';
import { Input, notification } from 'antd';
import axios from 'axios';

const { TextArea } = Input;

class Contact extends React.Component {
    state = {
        name: "",
        mail: "",
        phone: "",
        msg: "",
    }

    sendmsg(e) {
        e.preventDefault();
        var url = "https://api.telegram.org/bot1078689601:AAGCtCBzv0KnEpLgQRDGhSoy5ZCV2i9xjiM/sendMessage";
        var ltext = 'Tn: ' + this.state.name + '\n' + 'Email: ' + this.state.mail + '\n' + 'Phone: ' + this.state.phone + '\n' + 'Msg: ' + this.state.msg;
        axios.get(url, {
            params: {
                chat_id: 786590522,
                parse_mode: "Markdown",
                text: ltext,
            }
        }).then(() => {
            notification.success({
                message: 'Success',
                description: 'Gi thng tin thnh cng'
            })
        }).catch(() => {
            notification.error({
                message: 'Error',
                description: 'Gi thng tin tht bi'
            })
        })
        this.setState({
            name: "",
            mail: "",
            phone: "",
            msg: "",
        });
    }

    render() {

        return (
            <div className="contact-page">
                <div className="contact-box">
                    <div className="contact-info">
                        <h1>Lin h vi chng ti</h1>
                        <p>Nu bn c nhu cu hay thc mc, hy lin h vi chng ti qua form di y, chng ti s tr li qua email ca bn sm nht ngay sau khi nhn c thng tin.</p>
                    </div>
                    <form id="contact-form" onSubmit={this.sendmsg.bind(this)} ref="contactform">
                        <h1>Gi tin nhn</h1>
                        <div className="text-fields">
                            <div className="float-input">
                                <Input onChange={(e) => this.setState({name: e.target.value})} value={this.state.name} name="name" id="name" placeholder="Tn ca bn" required />
                                <span><i className="fa fa-user"></i></span>
                            </div>
                            <div className="float-input">
                                <Input onChange={(e) => this.setState({mail: e.target.value})} value={this.state.mail} name="mail" id="mail" placeholder="e-mail" required />
                                <span><i className="fa fa-envelope-o"></i></span>
                            </div>
                            <div className="float-input">
                                <Input onChange={(e) => this.setState({phone: e.target.value})} value={this.state.phone} name="phone" id="phone" placeholder="S in thoi" />
                                <span><i className="fa fa-phone"></i></span>
                            </div>
                        </div>

                        <div className="submit-area">
                            <TextArea onChange={(e) => this.setState({msg: e.target.value})} value={this.state.msg} style={{ height: "130px" }} name="comment" id="comment" placeholder="Tin nhn" required></TextArea>
                            <input type="submit" id="submit_contact" className="main-button" value="Send Now" />
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}

export default Contact;