import React from 'react';
import LinkPost from './components/linkpostitem';
import StandardPost from './components/standardpostitem';
import Masonry from 'react-masonry-component';

export default function PostList(props) {
    return (
        <div className="blog-page">
            <Masonry
                className={'blog-box'}
            >
                <LinkPost content="Click vo y  dn n ch stock" to="/stock-market" linkcontent="Click vo y"/>
                {
                    props.posts.map(
                        function(value,index) {
                            return (
                                <StandardPost title={value.title} img={value.image} date={value.date} to={"/post/" + value.id} />
                            )
                        }
                    )
                }
            </Masonry>
            {/* <a className="blog-page-link" href="#">Older Posts</a> */}
        </div>
    )
}