import React from 'react';
import { Link } from 'react-router-dom';


export default function LeftMenu(props) {
    return (
        <header>
            <div className="logo-box">
                <Link className="logo" to="/"><img alt="" src={`${process.env.PUBLIC_URL}/static/images/logo.png`}/></Link>
                <p className="slogan">Dont judge me</p>
            </div>

            <a className="elemadded responsive-link">Menu</a>

            <div className="menu-box">
                <ul className="menu">
                    <li><Link className="active" to="/stock-market">ch stock</Link></li>
                    <li><Link to="/">Home</Link></li>
                    <li><Link to="/about">About</Link></li>
                    <li><Link to="/">Portfolio</Link></li>
                    <li><Link to="/contact">Contact</Link></li>
                </ul>				
            </div>

            {/* <div className="archives-box">
                <h2>Archives</h2>
                <ul className="archives">
                    <li><a href="#">December 2013</a></li>
                    <li><a href="#">November 2013</a></li>
                    <li><a href="#">March 2013</a></li>
                    <li><a href="#">September 2012</a></li>
                    <li><a href="#">April 2012</a></li>
                    <li><a href="#">Jannuary 2012</a></li>
                </ul>				
            </div> */}

            <div className="categories-box tags-box">
                <h2>Categories</h2>
                <ul className="categories">
                    {
                        props.cats.map(
                            function(value, index) {
                                return (
                                    <li key={"cats"+index}>
                                        <Link to={"/category/"+value}>{value}</Link>
                                    </li>
                                )
                            }
                        )
                    }
                </ul>
            </div>

            <div className="social-box">
                <ul className="social-icons">
                    <li><a href="https://www.facebook.com/luthebao97/"><i className="fa fa-facebook-square"></i></a></li>
                    <li><a href="https://www.facebook.com/luthebao97/"><i className="fa fa-twitter-square"></i></a></li>
                    <li><a href="#"><i className="fa fa-rss-square"></i></a></li>
                    <li><a href="#"><i className="fa fa-google-plus-square"></i></a></li>
                    <li><a href="#"><i className="fa fa-linkedin-square"></i></a></li>
                    <li><a href="#"><i className="fa fa-pinterest-square"></i></a></li>
                    <li><a href="#"><i className="fa fa-flickr"></i></a></li>
                    <li><a href="#"><i className="fa fa-youtube-square"></i></a></li>
                    <li><a href="#"><i className="fa fa-instagram"></i></a></li>
                </ul>
            </div>
            <p className="copyright">&#169; 2020 BaoLT, All Rights Reserved</p>
        </header>
    )
}