import React from 'react';
import axios from 'axios';
import * as api from '../../api';

class PostDetail extends React.Component {
    state = {
        loaded: false,
        content: "",
        title: "",
        labels: [],
        image: "",
        date: ""
    }

    componentDidMount() {
        const myurl = api.api_post_detail.replace('221219972212199722121997', this.props.match.params.postid)
    
        axios.get(myurl).then(resp => {
            const content = resp.content;
            const parser = new DOMParser();
            const html = parser.parseFromString(content, 'text/html');
            const img = html.querySelector('img');
            const url = img ? img.src : '';
            this.setState({
                image: url,
                loaded: true,
                content: resp.data.content,
                title: resp.data.title,
                labels: resp.data.labels,
                date: resp.data.published.slice(0,10),
            })
        })
    }

    render() {
        let self = this;
        return (
            <div class="single-project">
                {
                    this.state.loaded === false ?
                    <></>
                    :
                    <div class="single-box">
                        <div class="single-box-content">
                            <div class="flexslider">
                                {/* <ul class="slides">
                                    <li>
                                        <img alt="" src={this.state.image} />
                                    </li>
                                </ul> */}
                            </div>
                            <div class="project-post-content">
                                <div class="project-text">
                                    <h1>{this.state.title}</h1>
                                    <div id="bodytextxxx" style={{ textAlign: "center" }} dangerouslySetInnerHTML={{__html: this.state.content}} />
                
                                </div>
                            </div>
                        </div>
                        <div class="sidebar">
                            <div class="post-info">
                                <h1>Post Info</h1>
                                <ul>
                                    <li>
                                        <span><i class="fa fa-user"></i></span><a href="#">BaoLT</a>
                                    </li>
                                    <li>
                                        <span><i class="fa fa-heart"></i></span><a href="#">999 Likes</a>
                                    </li>
                                    <li>
                                        <span><i class="fa fa-calendar"></i></span><a href="#">{this.state.date}</a>
                                    </li>
                                </ul>
                            </div>
                            <div class="tags-box">
                                <h1>Tags</h1>
                                <ul>
                                    {
                                        this.state.labels.map((value) => (
                                            <li>
                                                <a>{value}</a>
                                            </li>
                                        ))
                                    }
                                </ul>
                            </div>
                        </div>
                    </div>
                }
            </div>
        )
    }
}

export default PostDetail;